import React, { useState } from 'react'
import axios from 'axios'
import {
  LoginForm,
  ProConfigProvider,
  ProFormCaptcha,
  // eslint-disable-next-line no-unused-vars
  ProFormCheckbox,
  ProFormText
} from '@ant-design/pro-components'
import PropTypes from 'prop-types'
import {
  // eslint-disable-next-line no-unused-vars
  AlipayCircleOutlined,
  LockOutlined,
  MobileOutlined,
  // eslint-disable-next-line no-unused-vars
  TaobaoCircleOutlined,
  UserOutlined,
  // eslint-disable-next-line no-unused-vars
  WeiboCircleOutlined
} from '@ant-design/icons'
import { withRouter } from 'react-router-dom'
import { message, Tabs, ConfigProvider, Button } from 'antd'
import './login.css'
import Footcommon from '../../component/footcommon/foot_common'

// import Footcommon from '../../component/footcommon/foot_common'
// import mylogo from '../../homeviews/img/htgl-bg.png'
function Login (props) {
  const [loginType, setLoginType] = useState('account')
  // 登录的axios fetchData
  const headers = {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
  async function fetchData2 (values) {
    const data = new URLSearchParams()
    data.append('username', values.username)
    data.append('password', values.password)
    try {
      // eslint-disable-next-line no-unused-vars
      const response = await axios.post('http://47.237.100.43:8080/login', data, {
        headers
      })
      console.log('ddddddddddddddddddddd' + response)
      if (response.data.data.authorities[0].authority === 'ROLE_SYSTEM') {
        localStorage.setItem(
          'authorizationToken',
          response.headers.get('authorization')
        )
        sessionStorage.setItem('token2', JSON.stringify(response.data))
        props.history.push('/loginsuccess')
        window.open('#/administrator/home', '_blank')
        message.success('登录成功')
      } else {
        message.error('没有权限')
      }
      message.success('登录成功')
    } catch (error) {
      message.error('用户名或密码不匹配')
      console.log(error)
    }
  }
  return (
    <div style={{ backgroundColor: '#f2f4f3' }}>
      <div className="Login-content">
        <div className="Login-content-left"></div>
        <div className="Login-content-right">
          <div style={{ display: 'flex', flexDirection: 'row-reverse' }}>
          <Button
          type="text"
          style={{
            fontSize: '16px',
            width: 64,
            height: 64
          }}
          onClick={() => {
            props.history.push('/home')
          }}
        >
          首页
        </Button>
          </div>
          <ConfigProvider theme={{
            token: {
              colorPrimary: 'rgb(255, 144, 16)'
            }
          }}>
          <ProConfigProvider hashed={false}>
            <div style={{ backgroundColor: 'white' }}>
              <LoginForm
                title="管理员登录"
                onFinish={(values) => {
                  console.log('用户输入的值：', values)
                  fetchData2(values)
                }}
              >
                <Tabs
                  centered
                  activeKey={loginType}
                  onChange={(activeKey) => setLoginType(activeKey)}
                >
                  <Tabs.TabPane key={'account'} tab={'账号密码登录'} />
                  <Tabs.TabPane key={'phone'} tab={'手机号登录'} />
                </Tabs>
                {loginType === 'account' && (
                  <>
                    <ProFormText
                      name="username"
                      fieldProps={{
                        size: 'large',
                        prefix: <UserOutlined className={'prefixIcon'} />
                      }}
                      placeholder={'请输入用户名'}
                      rules={[
                        {
                          required: true,
                          message: '请输入用户名!'
                        }
                      ]}
                    />
                    <ProFormText.Password
                      name="password"
                      fieldProps={{
                        size: 'large',
                        prefix: <LockOutlined className={'prefixIcon'} />
                      }}
                      placeholder={'请输入密码'}
                      rules={[
                        {
                          required: true,
                          message: '请输入密码！'
                        }
                      ]}
                    />
                  </>
                )}
                {loginType === 'phone' && (
                  <>
                    <ProFormText
                      fieldProps={{
                        size: 'large',
                        prefix: <MobileOutlined className={'prefixIcon'} />
                      }}
                      name="mobile"
                      placeholder={'手机号'}
                      rules={[
                        {
                          required: true,
                          message: '请输入手机号！'
                        },
                        {
                          pattern: /^1\d{10}$/,
                          message: '手机号格式错误！'
                        }
                      ]}
                    />
                    <ProFormCaptcha
                      fieldProps={{
                        size: 'large',
                        prefix: <LockOutlined className={'prefixIcon'} />
                      }}
                      captchaProps={{
                        size: 'large'
                      }}
                      placeholder={'请输入验证码'}
                      captchaTextRender={(timing, count) => {
                        if (timing) {
                          return `${count} ${'获取验证码'}`
                        }
                        return '获取验证码'
                      }}
                      name="captcha"
                      rules={[
                        {
                          required: true,
                          message: '请输入验证码！'
                        }
                      ]}
                      onGetCaptcha={async () => {
                        message.success('获取验证码成功！验证码为：1234')
                      }}
                    />
                  </>
                )}
                <div
                >
                  <a
                    style={{
                      float: 'left'
                    }}
                    onClick={() => {
                      props.history.push('/register/registerselect')
                    }}
                    className='myorange'
                  >
                    申请账号
                  </a>
                  <a
                    style={{
                      float: 'right'
                    }}
                    onClick={() => {
                      props.history.push('/forget')
                    }}
                    className='myorange'
                  >
                    忘记密码
                  </a>
                </div>
              </LoginForm>
            </div>
          </ProConfigProvider>
          </ConfigProvider>
      </div>
      </div>
    <Footcommon></Footcommon>
    </div>
  )
}
export default withRouter(Login)
Login.propTypes = {
  history: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired
}
